body {
    margin: 0;
    padding: 0;
    background-color: rgb(252,252,252);
    color: rgb(102,102,102);
    font-family: "PT Sans Narrow",sans-serif;
}

.col-centered {
    float: none;
    margin: auto auto;
}

.my-container {
    width: 100%;
    padding: 0 0 0 0;
}

a {
    text-decoration: none;
    color: rgb(102,102,102);
}

.header.home {
    width: 100%;
    margin-top: 20px;

    text-align: center;

    background-color: white;
    border-top: 1px solid rgba(0,0,0, 0.08);
    border-bottom: 1px solid rgba(0,0,0, 0.08);
}

.header.home .logo {
    width: 12em;
    height: 6em;

    vertical-align: middle;
}

.header.home .title {
    vertical-align: middle;
    font-size: 2em;
    line-height: 3em;
}

.header.home .admin {
    text-align: center;
    border-left: 1px solid rgba(0,0,0, 0.08);
    border-right: 1px solid rgba(0,0,0, 0.08);
    font-size: 1em;
    margin-top: 1.79em;
    margin-bottom: 1.79em;
}

.header.home .admin:hover {
    cursor: pointer;
    background-color: #4593BC;
    color: white;
}

.content {
    width: 100%;
    text-align: center;
    padding: 40px 0 80px 0;
}

.content .wrapper {
    width: 90%;
    max-width: 300px;
    margin: 0 20px;
    padding-bottom: 20px;
    display: inline-block;

    background-color: rgba(69, 147, 188, 0.08);
    border-radius: 2px;

}

.content .title {
    padding: 15px;

    font-size: 20px;
    color: rgb(102, 102, 102);
}

.content .icon {
    display: inline-block;
    width: 100px;
    height: 100px;
    margin: 0 16px 10px ;
    -webkit-box-sizing: content-box;
            box-sizing: content-box;

    border: 4px solid rgb(69, 147, 188);
    border-radius: 52px;
}

.content .icon img {
    margin: 33px;
    width: 34px;
    height: 34px;
}

.content .button {
    display: block;
    margin: 0.6em auto 0.6em auto;
    width: 80%;
}

.registration-textarea {
    border: 1px solid #e5e5e5;
    border-radius: 10px;
    height: 200px;
    overflow: auto;
    padding: 10px;
}

.content .login.button {
    margin-right: 0;
}

.content .register.button {
    margin-left: 0;
}

.footer {
    width: 100%;

    background-color: rgb( 240, 240, 240);
    border-top: 1px solid rgba(0,0,0, 0.08);

    text-align: center;
}

.footer .external {
    margin: 1em 0.5em;
    display: inline-block;
}

.footer img {
    height: 60px;
}

@media screen and (min-height: 700px ){

    .content {
        padding-top: 100px;
    }
}

#user-role-input-group {
    width: 100%;
}

.input-group {
    margin-bottom: 0.5em;
}

ol li p {
    font-weight: normal;
}

.wordwrap {
    white-space: pre-wrap;      /* CSS3 */
    white-space: -moz-pre-wrap; /* Firefox */
    white-space: -pre-wrap;     /* Opera <7 */
    white-space: -o-pre-wrap;   /* Opera 7 */
    word-wrap: break-word;      /* IE */
}

@media screen and (max-width: 768px) {
    #registration-modal .modal-dialog {
        width: 90vw
    }
}

@media screen and (min-width: 768px) and (max-width: 992px) {
    #registration-modal .modal-dialog {
        width: 90vw
    }
}

@media screen and (min-width: 992px) and (max-width: 1200px) {
    #registration-modal .modal-dialog {
        width: 70vw
    }
}

@media screen and (min-width: 1200px) {
    #registration-modal .modal-dialog {
        width: 50vw
    }
}

